@charset "utf-8";

/* =========================================================
PARCO Header
========================================================= */
.phd {
  padding: 0 10px;
  background: #fff;
  border-bottom: 1px solid #ccc;
  overflow: hidden;
  position: relative;
  z-index: 22;
  &__in {
    height: 55px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    box-sizing: border-box;
  }
  &__logo {
    a {
      display: inline-block;
      line-height: 0;
      font-size: 0;
      text-decoration: none;
    }
    &__txt {
      width: 13px;
      height: 26px;
      margin-right: 2px;
    }
    &__img {
      width: 92px;
      height: 27px;
      vertical-align: bottom;
      margin-bottom: -1px;
      &.parcoya-ueno {
        width: 155px;
        height: 25px;
      }
    }
  }
  &__sns {
      display: flex;
      align-items: center;
      justify-content: center;
    li {
      margin-right: 8px;
      &:last-child {
        margin-right: 0;
      }
      a {
        display: flex;
        align-items: center;
        width: 35px;
        height: 35px;
        background: #222;
        border-radius: 50%;
        @media screen and (min-width:769px){
          width: 28px;
          height: 28px;
          &:hover {
            opacity: 0.7;
          }
        }
        svg {
          fill: #FFF;
          width: 100%;
        }
      }
    }
  }
}
@media screen and (min-width:769px){
  .phd {
    padding: 0 20px;
    &__in {
      height: 64px;
    }
    &__logo {
      &__txt {
        display: inline-block;
        font-size: 1.5rem;
        width: 14px;
        height: 32px;
        margin-right: 4px;
        vertical-align: middle;
      }
      &__img {
        width: 120px;
        height: 36px;
        vertical-align: middle;
        &.parcoya-ueno {
          width: 205px;
          height: 34px;
        }
      }
    }
  }
}

.phd__sns li .phd__sns__item__link-facebook {
  background: transparent;
}
.phd__sns li .phd__sns__item__link-facebook svg {
  height: 35px;
  @media screen and (min-width:769px){
    height: 28px;
  }
}
.phd__sns li .phd__sns__item__link-twitter {
  background: #3AB1DB;
}
.phd__sns li .phd__sns__item__link-twitter svg {
  height: 16px;
  @media screen and (min-width:769px){
    height: 11px;
  }
}
.phd__sns li .phd__sns__item__link-lineat {
  background: transparent;
}
.phd__sns li .phd__sns__item__link-lineat svg {
  height: 35px;
  @media screen and (min-width:769px){
    height: 28px;
  }
}
.phd__sns li .phd__sns__item__link-instagram {
  background: #d93174;
}
.phd__sns li .phd__sns__item__link-instagram svg {
  height: 19px;
  @media screen and (min-width:769px){
    height: 15px;
  }
}
.phd__sns li .phd__sns__item__link-tiktok {
  background: #000000;
}
.phd__sns li .phd__sns__item__link-tiktok svg {
  height: 19px;
  @media screen and (min-width:769px){
    height: 17px;
  }
}



/* =========================================================
PARCO Footer
========================================================= */
.pft{
  padding: 45px 0 58px;
  background: #222;
  position: relative;
  @media screen and (min-width:769px){
    padding: 50px 0 40px;
  }
  &__in {
    position:relative;
    margin:0 auto;
    @media screen and (min-width:769px){
      max-width: 930px;
    }
  }
  &__logo {
    text-align: center;
    svg {
      fill: #fff;
    }
    a {
      text-decoration: none;
    }
    &__txt {
      width: 14px;
      height: 32px;
      margin-right: 4px;
    }
    &__img {
      width: 120px;
      height: 36px;
      vertical-align: bottom;
      margin-bottom: -1px;
      &.parcoya-ueno {
        width: 205px;
        height: 34px;
      }
    }
    &.nagoya {
      .pft__logo__txt {
        margin-right: 5px;
      }
    }
  }
  &__pageTop {
    right: 5px;
    bottom: calc(50% - 27px);
    position: absolute;
    z-index: 5;
    @media screen and (min-width:769px){
      display: none;
      top: auto;
      left: auto;
      margin: 0;
      right: 30px;
    }
    @media (max-width: 768px) {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      top: -23px;
      bottom: unset;
      left: calc(50% - 23px);
      right: unset;
    }
    a {
      display: inline-block;
      position: relative;
      width: 45px;
      height: 45px;
      text-indent: 100%;
      overflow: hidden;
      white-space: nowrap;
      background: url('/page/artandculture/assets/images/pagetopmb.svg') no-repeat;
      background-size: 100%;
      @media screen and (min-width:769px){
        width: 54px;
        height: 54px;
        background: url('/page/artandculture/assets/images/pagetop.svg') no-repeat;
      }
      &:hover {
        opacity: 0.7;
      }
    }
  }
  &__copyright {
    margin-top: 30px;
    text-align: center;
    line-height: 0;
    @media (max-width: 768px) {
      margin-top: 14px;
    }
    svg {
      height: 11px;
      fill: #fff;
      overflow: hidden;
    }
  }
}



.social-footer {
  padding: 28px 0 0 0;
  @media (max-width: 768px) {
    padding: 14px 0 0 0;
  }
  ul {
    display: flex;
    align-items: center;
    justify-content: center;
    li {
      padding: 0 12px;
      @media (max-width: 768px) {
        padding: 0 12px;
      }
    }
  }
  a {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  img {
    @media (max-width: 768px) {
      width: 26px;
      height: 26px;
    }
  }
}